.drawer{
    position: fixed;
    right: 0;
    top: 38px;
    z-index: 9999 !important;
    text-align: center;
    background: #FFFFFF;
    box-shadow: 0 0 4px -2px rgba(0,0,0,0.50);
    .drawer_title{
      padding-left: 10px;
      background: #F2F2F2;
      border-radius: 2px 2px 0 0;
      position: relative;
      .close{
        cursor: pointer;
        position: absolute;
        right: 5px;
        top: 0;
      }
    }
  }