.s_tooltip{
  z-index: 9999;
  position: relative;
  ul,li{ padding:0;margin:0;list-style:none}
a{
  text-decoration: none !important;
  color: #40404C;
  &:hover{
    color: inherit;
  }
}
input{ outline:none }
input[type='radio']{
  display: none;
}
input[type='radio']:checked + label .s_tooltip_wrapper__content__click {
  display: table;
  border-bottom: 1px solid #F0F0F0;
}
input[type='radio']:hover + label .s_tooltip_wrapper__content__hover {
  display: table;
  border-bottom: 1px solid #F0F0F0;
}
.s_tooltip_text{
  position: relative;
  .s_tooltip_list{
    position: relative;
  }
  label{
    .s_tooltip_icon{
      margin-bottom: 10px;
      transition: transform 0.3s;
      -webkit-transition:transform 0.3s;
      fill:  #40404C;
      color: #40404C;
      &:hover{
        cursor: pointer;
        fill: #FF4713;
        color: #FF4713;
      }
    }
    .s_tooltip_wrapper__content{
      display: none;
      background: #ffffff;
      padding: 2px 0;
      position: absolute;
      min-width: 100px;
      width: 100%;
      box-shadow: 0 0 8px 0 rgba(0,0,0,0.3);
      font-family: "PingFangSC-Regular";
      font-size: 12px;
      color: #40404C;
      line-height: 30px;
      left: 30px;
      top: 0px;
      .s_tooltip_title{
        text-align: left;
        margin: 0 10px;
        font-family: "PingFangSC-Medium";
        font-size: 10px;
        color: #A3A3A3;
        position: relative;
        white-space: nowrap;
        .add{
          cursor: pointer;
          position: absolute;
          right: 5px;
        }
      }
      .productInput{
        text-align: center;
        height: 30px;
        width: 100%;
      }
      li{
        white-space: nowrap;
        padding: 0 30px;
        &:hover{
          background: #FEECE7;
        }
      }
      .sv_card{
        .sv_cardtitle{
          background: #F2F2F2;
          border-radius: 2px 2px 0 0;
          font-family: "PingFangSC-Regular";
          font-size: 12px;
          color: #40404C;
          line-height: 30px;
          padding: 0 10px;
        }
      }
    }
  }
}
}