input{ outline:none; }
a{
  text-decoration: none !important;
  &:hover{
    color: inherit;
  }
}
input[type='checkbox']{
  display: none;
}
input[type='checkbox']:checked + label .sider_menu {
  display: none;
  border-bottom: 1px solid #F0F0F0;
}

.sider_inner{
  display: flex;
  height: 100%;
  .part{
    width: 30px;
    background: #FFFFFF;
    border-radius: 0 2px 2px 0;
    height: calc(~"100vh-38px");
    border-right: 1px solid #DDDDDD;
    text-align: center;
    padding: 10px 0;
    /deep/.s_tooltip{
        position: fixed;
        width: 30px;
    }
    .partment_input{
      position: relative;
      height: 30px;
      z-index: 10000;
    }
    .more{
      position: absolute;
      bottom: 60px;
      left: 5px;
      cursor: pointer;
    }
  }
  input[type='checkbox']:checked + label .more_wrapper__content {
    display: block;
    border-bottom: 1px solid #F0F0F0;
  }
  .more_wrapper__content{
    display: none;
    background: #fff;
    position: fixed;
    min-width: 130px;
    min-height: 200px;
    box-shadow: 0 0 8px 0 rgba(0,0,0,0.3);
    font-family: "PingFangSC-Regular";
    font-size: 12px;
    color: #40404C;
    line-height: 30px;
    left: 30px;
    bottom: 60px;
    li{
      &:hover{
        cursor: pointer;
        background: #FEECE7;
      }
    }
  }
  .sider_menu{
    width: 140px;
    background: #FFFFFF;
    border: 1px solid #DDDDDD;
  }
  .title{
    height: 34px;
    line-height: 34px;
    font-size: 12px;
    text-align: center;
    border-bottom: 1px solid #DDDDDD;
  }
  ul {
    padding: 0;
    margin: 0;
  }
  li {
    list-style-type: none;
  }
  ::-webkit-scrollbar {
    display: none;
  }
}