.transfer{
    display: flex;
    input[type='checkbox']{
      cursor: pointer;
    }
    .transfer_icon{
      margin: 0 10px;
      cursor: pointer;
      margin-top: 20%;
    }
    .transfer_block{
      text-align: left;
      flex: 1;
      border: 1px solid #eee;
      height: 50vh;
      overflow: scroll;
      h3{
        padding-left: 20px;
      }
      .content{
        padding-left: 50px;
      }
    }
  }
  .button_contain{
    display: flex;
    width: 100%;
    justify-content: flex-end;
    margin-top: 50px;
    text-align: center;
    .button_reset{
      width: 100px;
      border: 1px solid #40404C;
      border-radius: 2px;
      cursor: pointer;
      margin-right: 20px;
      &:hover{
        color: cornflowerblue;
      }
    }
    .button_save{
      width: 100px;
      cursor: pointer;
      background: #40404C;
      border: 1px solid #40404C;
      border-radius: 2px;
      color: #ffffff;
      &:hover{
        color: cornflowerblue;
      }
    }
  }