.drawer3{
position: fixed;
top: 38px;
left: 30px;
right: 0;
bottom: 0;
background: #ffffff;
.transfer_contain{
  padding: 0 30px;
}
}
input[type='checkbox']{
  display: none;
}
.header_wrapper__content{
  display: none;
  background: #ffffff;
  position: absolute;
  top: 25px;
  text-align: center;
  width: 100px;
}
input[type='checkbox']:hover + label .header_wrapper__content {
    display: block;
    border-bottom: 1px solid #F0F0F0;
  }
.main{
    @headerH: 38px;
    @siderW: 30px;
    width: 100vw;
    height: 100vh;
    background: #f7f7f7;
    display: flex;
    .header{
      display: flex;
      justify-content: space-between;
      position: fixed;
      z-index: 9999;
      top: 0;
      left: 0;
      right: 0;
      padding-right: 24px;
      height: @headerH;
      line-height: @headerH;
      background: #ffffff;
      box-shadow: 0 0 8px 1px rgba(0,0,0,0.10);
      .img_outer{
        width: @siderW+140px;
        height: @headerH;
        line-height: @headerH;
        display: flex;
        background: #ffffff;
        .svg{
          height: 100%;
          width: 30px;
          display: flex;
          align-items: center;
          justify-content: center;
        }
        .title{
          flex: 1;
          text-align: center;
          font-family: "MFLangQian_Noncommercial-Regular";
          font-size: 18px;
          color: #FF4713;
        }
      }
      .header_right{
        display: flex;
        font-family: "PingFangSC-Regular";
        font-size: 12px;
        color: #40404C;
        cursor: pointer;
        span{
          margin-left: 20px;
          &:hover{
            color: #FF4713;
          }
        }
        a{
          &:hover{
            color: #FF4713;
          }
        }
        img{
          width: 20px;
          height: 20px;
          margin: 6px 0 8px 20px;
          cursor: pointer;
        }
        .drawer_content{
          img{
            margin: 0;
          }
        }
        /deep/.s_tooltip{
          float: left;
          display: flex;
          .s_tooltip_text{
            display: flex;
          }
          .s_tooltip_wrapper__content{
            width: 110px;
            top: 30px;
            left: -15px;
          }
          &:nth-child(2){
            .s_tooltip_wrapper__content{
              cursor: pointer;
              left: -48px;
              text-align: center;
              .title{
                display: flex;
                justify-content: center;
              }
              .footer{
                background: #F2F2F2;
                border-radius: 2px 2px 0 0 0;
                text-align: center;
              }
            }
          }
        }
      }
    }
    .sider{
      position: fixed;
      display: inline-table;
      top:  @headerH;
      bottom: 0;
      left: 0;
      overflow: scroll;
      z-index: 9999;
      height: 100%;
      .triger{
        position: absolute;
        right: 0;
        top: 8px;
      }
    }
    .content_outer{
      padding-top: @headerH;
      padding-left: @siderW;
      flex: 1;
      .content_middle{
        position: absolute;
        top: @headerH;
        left: @siderW;
        bottom: 0;
        right: 0;
        overflow: scroll;
      }
    }
}